import React, {Component} from 'react';
import './Freezer.css';

class ContentRow extends Component {
    constructor(props) {
        super(props);
        this.minusClicked = this.minusClicked.bind(this);
        this.plusClicked = this.plusClicked.bind(this);
        this.descriptionClicked = this.descriptionClicked.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.onEditedDescription = this.onEditedDescription.bind(this);
        this.cancelEditing = this.cancelEditing.bind(this);
        this.state = {
            editedDescription: null,
        }
    }

    minusClicked() {
        this.props.onMinusClicked(this.props.id);
    }

    plusClicked() {
        this.props.onPlusClicked(this.props.id);
    }

    descriptionClicked() {
        this.setState({editedDescription: this.props.description});
    }

    onEditedDescription(event) {
        let newDescription = event.target.value;
        this.setState({editedDescription: newDescription});
    }

    handleSubmit(event) {
        event.preventDefault();
        let newDescription = event.target.description.value;
        this.setState({editedDescription: null});
        this.props.onDescriptionChanged(this.props.id, newDescription);
    }

    cancelEditing() {
        this.setState({editedDescription: null})
    }

    render() {
        let descriptionField = this.state.editedDescription != null
            ? <form onSubmit={this.handleSubmit}>
                <input
                    autoFocus
                    type="text"
                    className="mr-2"
                    name="description"
                    value={this.state.editedDescription}
                    onChange={this.onEditedDescription}
                    onKeyDown={event => { if (event.keyCode === 27) this.cancelEditing(); }}
                />
                <input type="submit" className="btn btn-primary mr-2" value="Ok" />
                <button type="button" className="btn btn-secondary mr-2" onClick={this.cancelEditing}>Cancel</button>
            </form>
            : <span className="item-description" onClick={this.descriptionClicked}>
                {this.props.description}
            </span>;

        let optionalTimer = this.props.quantity === 0
            ? <CountdownTimer
                numSeconds="5"
                ignite={() => this.props.onDeleteRow(this.props.id)}
            />
            : null;

        let currentQuantity = this.props.quantity;

        let minusButton = currentQuantity > 0
            ? <button
                className="btn btn-outline-primary btn-sm mr-3"
                onClick={this.minusClicked}>
                -
            </button>
            : <button
                className="btn btn-outline-primary btn-sm mr-3"
                disabled>
                -
            </button>;
        let plusButton = <button
            className="btn btn-outline-primary btn-sm ml-3"
            onClick={this.plusClicked}>
            +
        </button>;

        return (
            <tr>
                <td>
                    {descriptionField}
                </td>
                <td>
                    {minusButton}
                    {currentQuantity}
                    {plusButton}
                    {optionalTimer}
                </td>
            </tr>);
    }
}

class ContentList extends Component {
    render() {
        const rows = this.props.items.map((item) =>
            <ContentRow
                key={item.id.toString()}
                id={item.id}
                description={item.description}
                quantity={item.quantity}
                onMinusClicked={this.props.onDecrementItemQuantity}
                onPlusClicked={this.props.onIncrementItemQuantity}
                onDescriptionChanged={this.props.onChangeItemDescription}
                onDeleteRow={this.props.onDeleteRow}
            />
        );
        return (
            <table className="table table-striped">
                <thead>
                <tr><th>Description</th><th>Quantity</th></tr>
                </thead>
                <tbody>
                {rows}
                </tbody>
            </table>
        );
    }
}

class CountdownTimer extends Component {
    constructor(props) {
        super(props);
        this.state = {
            countdown: props.numSeconds,
        }
    }

    componentDidMount() {
        this.timerId = setInterval(() => this.tick(), 1000);
    }

    componentWillUnmount() {
        clearInterval(this.timerId);
    }

    tick() {
        this.setState(state => {
            let newCount = state.countdown - 1;
            if (newCount <= 0) {
                clearInterval(this.timerId);
                this.props.ignite();
            }
            return {countdown: newCount}
        });
    }

    render() {
        return this.state.countdown > 0
            ? <span>
                <span className="spinner-border spinner-border-sm mr-2 ml-3 text-danger" />
                {this.state.countdown}
            </span>
            : null;
    }
}

class Freezer extends Component {
    constructor(props) {
        super(props);
        this.decrementQuantity = this.decrementQuantity.bind(this);
        this.incrementQuantity = this.incrementQuantity.bind(this);
        this.updateDescription = this.updateDescription.bind(this);
        this.initiateAddItem = this.initiateAddItem.bind(this);
        this.newItemDescriptionChanged = this.newItemDescriptionChanged.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
        this.cancelAddItem = this.cancelAddItem.bind(this);
        this.filterChange = this.filterChange.bind(this);
        this.deleteItem = this.deleteItem.bind(this);
        this.state = {
            items: props.initialItems,
            highestItemId: Math.max(...props.initialItems.map((item) => item.id)),
            addingItem: false,
            newItemDescription: "",
            filter: "",
        };
    }

    decrementQuantity(id) {
        this.setState((state) => {
            let items = state.items; // TODO: Don't mutate previous state.
            let index = items.findIndex((item) => item.id === id);
            if (index < 0) return;
            let currentItem = items[index];
            let currentQuantity = currentItem.quantity;
            if (currentQuantity <= 0) return;
            let newQuantity = currentQuantity - 1;
                items[index].quantity = newQuantity;
            return { items };
        });
    }

    incrementQuantity(id) {
        this.setState((state) => {
            let items = state.items; // TODO: Don't mutate previous state.
            let index = items.findIndex((item) => item.id === id);
            if (index < 0) return;
            items[index].quantity++;
            return { items };
        });
    }

    updateDescription(id, newDescription) {
        this.setState(state => {
            let items = state.items; // TODO: Don't mutate previous state.
            let index = items.findIndex((item) => item.id === id);
            if (index < 0) return;
            items[index].description = newDescription;
            return { items };
        });
    }

    initiateAddItem() {
        this.setState({ addingItem: true });
    }

    newItemDescriptionChanged(e) {
        this.setState({newItemDescription: e.target.value});
    }

    handleSubmit(event) {
        this.setState((prevState) => {
            let items = prevState.items; // TODO: Don't mutate previous state.
            let newItemId = prevState.highestItemId + 1;
            let newItem = { id: newItemId, description: prevState.newItemDescription, quantity: 1 };
            items.push(newItem);
            return {
                items,
                highestItemId: newItemId,
                newItemDescription: ""
            };
        });
        event.preventDefault();
    }

    cancelAddItem() {
        this.setState({ addingItem: false, newItemDescription: "" });
    }

    filterChange(e) {
        this.setState({filter: e.target.value});
    }

    deleteItem(id) {
        this.setState((state) => {
            let items = state.items; // TODO: Don't mutate previous state.
            let index = items.findIndex((item) => item.id === id);
            if (index < 0) return;
            items.splice(index, 1);
            return { items };
        });
    }

    render() {
        let addItemArea = !this.state.addingItem
            ? <button
                type="button"
                className="btn btn-primary btn-lg ml-2"
                onClick={this.initiateAddItem}>
                + Add item(s)
            </button>
            : <form onSubmit={this.handleSubmit}>
                <input
                    autoFocus
                    type="text"
                    className="mr-2 ml-2"
                    name="description"
                    value={this.state.newItemDescription}
                    onChange={this.newItemDescriptionChanged}
                    onKeyDown={event => { if (event.keyCode === 27) this.cancelAddItem(); }}
                />
                <input type="submit" className="btn btn-primary mr-2" value="Add" />
                <button type="button" className="btn btn-secondary mr-2" onClick={this.cancelAddItem}>Cancel</button>
            </form>;

        let filterStr = this.state.filter.toLowerCase();
        let filteredItems = this.state.filter.length > 0
            ? this.state.items.filter(item => item.description.toLowerCase().includes(filterStr))
            : this.state.items;
        return (
            <div>
                <h1>In my freezer</h1>
                <span className="ml-2">Filter:</span>
                <input
                    type="text"
                    name="filter"
                    className={"ml-2 mb-2"}
                    value={this.state.filter}
                    onChange={this.filterChange}
                    onKeyDown={event => { if (event.keyCode === 27) this.setState({filter: ""}); }}
                />
                <ContentList
                    items={filteredItems}
                    onDecrementItemQuantity={this.decrementQuantity}
                    onIncrementItemQuantity={this.incrementQuantity}
                    onChangeItemDescription={this.updateDescription}
                    onDeleteRow={this.deleteItem}
                />
                {addItemArea}
            </div>
        );
    }
}

export default Freezer;



