import React from 'react';
import ReactDOM from 'react-dom';
import Freezer from './Freezer';
import './index.css';

ReactDOM.render(
  <Freezer
      initialItems={[
          { id: 1, description: "Pizza Grandiosa", quantity: 2 },
          { id: 2, description: "Fiskekaker", quantity: 7 },
          { id: 3, description: "Scampi", quantity: 1 },
          { id: 4, description: "Fiskegrateng", quantity: 1 },
          { id: 5, description: "Isbiter", quantity: 2 },
      ]}/>,
  document.getElementById('root')
);



